/* ====== Thiết lập kiểu chung ====== */

html,
body {
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
  overflow: hidden;
}

/* ====== Ảnh nền full màn hình ====== */

.bg-cover {
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  background-attachment: fixed;
  height: 100vh;
  width: 100vw;
}

/* ====== Hiệu ứng overlay - Làm tối nền ====== */

.overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6);
}

/* ====== Form đăng nhập ====== */

.login-container {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: rgba(255, 255, 255, 0.95);
  padding: 25px;
  border-radius: 12px;
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.3);
  width: 420px;
}

/* ====== Input fields ====== */

.login-container input {
  width: 100%;
  padding: 12px;
  margin-top: 8px;
  border-radius: 8px;
  border: 1px solid #ccc;
  font-size: 16px;
  transition: border 0.3s ease-in-out;
}

.login-container input:focus {
  border: 1px solid #28a745;
  outline: none;
}

/* ====== Căn chỉnh Captcha ====== */

.captcha-container {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-top: 8px;
}

.captcha-img {
  height: 40px;
  width: 100px;
  border-radius: 6px;
}

/* ====== Căn chỉnh nút ====== */

.btn-green {
  width: 100%;
  padding: 12px;
  font-size: 16px;
  background: #28a745;
  color: white;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: background 0.3s ease-in-out;
  margin-top: 10px;
}

.btn-green:hover {
  background: #218838;
}

/* Nhóm các nút "立即注册" và "忘记密码" */

.btn-group {
  display: flex;
  justify-content: space-between;
  margin-top: 12px;
}

.btn-outline {
  width: 48%;
  padding: 12px;
  font-size: 14px;
  background: white;
  color: #28a745;
  border: 2px solid #28a745;
  border-radius: 8px;
  cursor: pointer;
  transition: background 0.3s ease-in-out, color 0.3s ease-in-out;
}

.btn-outline:hover {
  background: #28a745;
  color: white;
}

/* ====== MODAL ĐĂNG KÝ & QUÊN MẬT KHẨU ====== */

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.4);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.modal-container {
  background: white;
  padding: 25px;
  border-radius: 12px;
  width: 450px;
  box-shadow: 0px 8px 16px rgba(0, 0, 0, 0.2);
  position: relative;
}

/* ====== Nút đóng "X" ====== */

.close-btn {
  position: absolute;
  top: 12px;
  right: 15px;
  background: transparent;
  border: none;
  font-size: 20px;
  color: black;
  cursor: pointer;
  transition: color 0.3s ease-in-out;
}

.close-btn:hover {
  color: red;
}

/* ====== Input trong modal ====== */

.modal-container input {
  width: 100%;
  padding: 12px;
  margin-top: 8px;
  border-radius: 8px;
  border: 1px solid #ccc;
  font-size: 16px;
  transition: border 0.3s ease-in-out;
}

.modal-container input:focus {
  border: 1px solid #28a745;
  outline: none;
}

/* ====== Tabs chọn phương thức xác thực ====== */

.tab-container {
  display: flex;
  justify-content: space-between;
  margin-bottom: 15px;
  border-bottom: 2px solid #ddd;
}

.tab {
  flex: 1;
  text-align: center;
  padding: 10px;
  cursor: pointer;
  background: white;
  border: none;
  color: #333;
  font-size: 14px;
}

.tab-active {
  flex: 1;
  text-align: center;
  padding: 10px;
  cursor: pointer;
  background: #28a745;
  color: white;
  font-size: 14px;
  font-weight: bold;
  border-radius: 8px 8px 0 0;
}

/* ====== QR Code Placeholder ====== */

.qr-placeholder {
  width: 180px;
  height: 180px;
  background: #f5f5f5;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 14px;
  color: #666;
  border: 1px solid #ddd;
  border-radius: 8px;
  margin: 10px auto;
}

/* ====== Hiệu ứng fadeIn cho modal ====== */

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: scale(0.95);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}

.modal-container {
  animation: fadeIn 0.3s ease-in-out;
}
